@import './Color.scss';

.title {
    position: relative;
    margin-bottom: 30px;
    padding-bottom: 2px;

    h3 {
        font-size: 20px;
        font-weight: 400;
    }

    &::before {
        position: absolute;
        content: '';
        bottom: -5px;
        left: 0;
        background-color: $fontColor;
        width: 120px;
        height: 3px;
        border-radius: 100px;
    }
    &::after {
        position: absolute;
        content: '';
        bottom: -5px;
        left: 0;
        background-color: $primaryColor;
        width: 32px;
        height: 3px;
        border-radius: 100px;
    }
}
